import React, { useState, useEffect } from "react";
import "./TimeTable.css";
import { useSelector } from "react-redux";

import church_1 from "../images/wedding-church-1.png";
import church_2 from "../images/wedding-church-2.png";
import church_3 from "../images/wedding-church-3.png";
import church_4 from "../images/wedding-church-4.png";
import church_5 from "../images/wedding-church-5.png";

import rings_1 from "../images/wedding-rings-1.png";
import rings_2 from "../images/wedding-rings-2.png";
import rings_3 from "../images/wedding-rings-3.png";
import rings_4 from "../images/wedding-rings-4.png";
import rings_5 from "../images/wedding-rings-5.png";

import coctail_1 from "../images/wedding-coctail-1.png";
import coctail_2 from "../images/wedding-coctail-2.png";
import coctail_3 from "../images/wedding-coctail-3.png";
import coctail_4 from "../images/wedding-coctail-4.png";
import coctail_5 from "../images/wedding-coctail-5.png";

import cake_1 from "../images/wedding-cake-1.png";
import cake_2 from "../images/wedding-cake-2.png";
import cake_3 from "../images/wedding-cake-3.png";
import cake_4 from "../images/wedding-cake-4.png";
import cake_5 from "../images/wedding-cake-5.png";

import cars_1 from "../images/wedding-cars-1.png";
import cars_2 from "../images/wedding-cars-2.png";
import cars_3 from "../images/wedding-cars-3.png";
import cars_4 from "../images/wedding-cars-4.png";
import cars_5 from "../images/wedding-cars-5.png";

const TimeTableEl = ({ titleTime, titleDescription, style, color }) => {
  return (
    <div key={titleTime} style={style} className="time-table-el">
      <div className="time-table-el-time" style={{ borderBottomColor: color }}>
        <h2 style={{ color: color }}>{titleTime}</h2>
      </div>
      <h2 style={{ color: color }}>{titleDescription}</h2>
    </div>
  );
};

const TimeTableIcon = ({ src, alt, style }) => {
  return <img src={src} alt={alt} style={style} />;
};

const TimeTable = () => {
  const marginBetweenEls = 15;
  const margintTopIcons = 12;
  const [sizeIcons, setSizeIcons] = useState(8);
  const { commonColor, iconNumberSuffix } = useSelector(
    (state) => state.app.timeTableColorStyle
  );
  const churches = [church_1, church_2, church_3, church_4, church_5];
  const rings = [rings_1, rings_2, rings_3, rings_4, rings_5];
  const coctailes = [coctail_1, coctail_2, coctail_3, coctail_4, coctail_5];
  const cakes = [cake_1, cake_2, cake_3, cake_4, cake_5];
  const cars = [cars_1, cars_2, cars_3, cars_4, cars_5];

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 768) {
        setSizeIcons(12);
      }
    };

    window.addEventListener("resize", handleResize);
    handleResize();

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const arrIcons = [
    {
      title: "Church",
      src: churches[iconNumberSuffix],
    },
    {
      title: "Rings",
      src: rings[iconNumberSuffix],
    },
    {
      title: "Coctail",
      src: coctailes[iconNumberSuffix],
    },
    {
      title: "Cake",
      src: cakes[iconNumberSuffix],
    },
    {
      title: "Cars",
      src: cars[iconNumberSuffix],
    },
  ];

  const arrTableTimeEvents = [
    {
      time: "9:30",
      event: "  ",
    },
    {
      time: "10:00",
      event: " ",
    },
    {
      time: "14:00",
      event: " ",
    },
    {
      time: "18:30",
      event: " ",
    },
    {
      time: "20:00",
      event: " ",
    },
  ];

  return (
    <div className="time-table">
      <div
        className="time-table-round"
        style={{ backgroundColor: commonColor }}
      />
      <div className="time-table-content">
        <div
          className="time-table-left"
          style={{ borderRightColor: commonColor }}
        >
          {arrTableTimeEvents.map((el, index) => {
            return index % 2 === 0 ? (
              <TimeTableEl
                titleTime={arrTableTimeEvents[index].time}
                titleDescription={arrTableTimeEvents[index].event}
                style={{
                  ...TimeTableElStyle,
                  marginLeft: "30%",
                  marginTop: `${marginBetweenEls}%`,
                }}
                color={commonColor}
              />
            ) : (
              <TimeTableIcon
                src={arrIcons[index].src}
                alt={arrIcons[index].title}
                style={{
                  width: `${sizeIcons}vw`,
                  height: "auto",
                  marginLeft: `calc(94% - ${sizeIcons}vw)`,
                  marginBottom: `${marginBetweenEls}%`,
                  marginTop: `${margintTopIcons + 3}%`,
                }}
              />
            );
          })}
        </div>
        <div className="time-table-right">
          {arrIcons.map((el, index) => {
            return index % 2 === 1 ? (
              <TimeTableEl
                titleTime={arrTableTimeEvents[index].time}
                titleDescription={arrTableTimeEvents[index].event}
                style={{
                  ...TimeTableElStyle,
                  marginTop: `${marginBetweenEls}%`,
                }}
                color={commonColor}
              />
            ) : (
              <TimeTableIcon
                src={arrIcons[index].src}
                alt={arrIcons[index].title}
                style={{
                  width: `${sizeIcons}vw`,
                  height: "auto",
                  marginTop: `${margintTopIcons}%`,
                  marginBottom: `${marginBetweenEls}%`,
                  marginLeft: "6%",
                }}
              />
            );
          })}
        </div>
      </div>
      <div
        className="time-table-round"
        style={{ backgroundColor: commonColor }}
      />
    </div>
  );
};

const TimeTableElStyle = {
  display: "flex",
  width: "70%",
  marginBottom: "2vw",
  textAlign: "center",
  flexDirection: "column",
};

export default TimeTable;
