import { useSelector } from "react-redux";
import "./ConfirmedArrive.css";

const ConfirmedArrive = () => {
  const { commonColor } = useSelector((state) => state.app.timeTableColorStyle);

  return (
    <div className="confirmed-arrive">
      <div
        className="confirmed-arrive-round"
        style={{ backgroundColor: commonColor }}
      />
      <div className="confirmed-arrive-content">
        <div
          className="confirmed-arrive-left"
          style={{ borderRightColor: commonColor }}
        >
          <div
            className="confirmed-arrive-left-content"
            style={{ borderColor: commonColor }}
          >
            <h2 style={{ color: commonColor }}></h2>
            <p style={{ color: commonColor }}>
              ,   
              <br />
               10.06.2025,    c
              <br />   !
            </p>
            <div
              className="confirmed-arrive-left-content-btn"
              style={{ color: commonColor, borderColor: commonColor }}
              onClick={() =>
                window.open(
                  "https://forms.gle/KkKpSujMU2JefUfj6",
                  "_blank"
                )
              }
            >
              
            </div>
          </div>
        </div>
        <div className="confirmed-arrive-right" />
      </div>
      <div
        className="confirmed-arrive-round"
        style={{ backgroundColor: commonColor }}
      />
    </div>
  );
};

export default ConfirmedArrive;
