import Content from './components/Content'
import AudioPlayer from './components/AudioPlayer'
import { useSelector } from 'react-redux';

const App = () => {
  const appBgColor = useSelector((state) => state.app.backgroundColor);

  return (
    <div style={{...AppStyle, backgroundColor: appBgColor}}>
      <Content/>
      <AudioPlayer/>
    </div>
  );
}

const AppStyle = { 
  minWidth: '98vw',
  minHeight: '100vh',
};

export default App;
