import { createSlice } from "@reduxjs/toolkit";

const colorStyles = {
  "#919682": {
    inviteColorStyle: {
      backgroundColor: "#ECE1DD",
      textColor: "#AF7E84",
      borderColor: "#AF7E84",
    },
    timeTableColorStyle: {
      commonColor: "#919682",
      iconNumberSuffix: 0,
    },
    sliderPlaceColorStyle: {
      backgroundColor: "#595E48",
      textColor: "#ECE1DD",
      headerColor: "#919682",
    },
    audioPlayer: {
      backgroundColor: "#595E48",
    }
  },
  "#595E48": {
    inviteColorStyle: {
      backgroundColor: "#919682",
      textColor: "#ECE1DD",
      borderColor: "#595E48",
    },
    timeTableColorStyle: {
      commonColor: "#595E48",
      iconNumberSuffix: 1,
    },
    sliderPlaceColorStyle: {
      backgroundColor: "#919682",
      textColor: "#ECE1DD",
      headerColor: "#595E48",
    },
    audioPlayer: {
      backgroundColor: "#919682",
    }
  },
  "#C9BFBC": {
    inviteColorStyle: {
      backgroundColor: "#C7A491",
      textColor: "#421716",
      borderColor: "#421716",
    },
    timeTableColorStyle: {
      commonColor: "#C9BFBC",
      iconNumberSuffix: 2,
    },
    sliderPlaceColorStyle: {
      backgroundColor: "#C7A491",
      textColor: "#421716",
      headerColor: "#C9BFBC",
    },
    audioPlayer: {
      backgroundColor: "#C7A491",
    }
  },
  "#A98B7B": {
    inviteColorStyle: {
      backgroundColor: "#EECFCC",
      textColor: "#AF7E84",
      borderColor: "#AF7E84",
    },
    timeTableColorStyle: {
      commonColor: "#A98B7B",
      iconNumberSuffix: 3,
    },
    sliderPlaceColorStyle: {
      backgroundColor: "#EECFCC",
      textColor: "#AF7E84",
      headerColor: "#A98B7B",
    },
    audioPlayer: {
      backgroundColor: "#EECFCC",
    }
  },
  "#AF7E84": {
    inviteColorStyle: {
      backgroundColor: "#ECE1DD",
      textColor: "#AF7E84",
      borderColor: "#AF7E84",
    },
    timeTableColorStyle: {
      commonColor: "#AF7E84",
      iconNumberSuffix: 4,
    },
    sliderPlaceColorStyle: {
      backgroundColor: "#ECE1DD",
      textColor: "#AF7E84",
      headerColor: "#AF7E84",
    },
    audioPlayer: {
      backgroundColor: "#ECE1DD",
    }
  },
};

const appSlice = createSlice({
  name: "app",
  initialState: {
    theme: colorStyles["#919682"],
    backgroundColor: "#919682",
    inviteColorStyle: colorStyles["#919682"].inviteColorStyle,
    timeTableColorStyle: colorStyles["#919682"].timeTableColorStyle,
    confirmedArriveColorStyle: {
      commonColor: "#919682",
    },
    sliderPlaceColorStyle: colorStyles["#919682"].sliderPlaceColorStyle,
    audioPlayer: colorStyles["#919682"].audioPlayer
  },
  reducers: {
    changeAppColor: (state, action) => {
      const selectedColor = action.payload;
      state.theme = colorStyles[selectedColor];

      state.backgroundColor = selectedColor;
      state.confirmedArriveColorStyle = {
        commonColor: selectedColor,
      };
      state.inviteColorStyle = state.theme.inviteColorStyle;
      state.timeTableColorStyle = state.theme.timeTableColorStyle;
      state.sliderPlaceColorStyle = state.theme.sliderPlaceColorStyle;
      state.audioPlayer = state.theme.audioPlayer;
    },
  },
});

export const { changeAppColor } = appSlice.actions;
export default appSlice.reducer;
