import Info from "./Info";
import Invite from "./Invite";
import TimeTable from "./TimeTable";
import DressCode from "./DressCode";
import ConfirmedArrive from "./ConfirmedArrive"
import SliderPlace from "./SliderPlace";

const Content = () => {
  return (
    <div style={{...ContentStyle}}>
      <Info/>
      <Invite/>
      <TimeTable/>
      <DressCode/>
      <ConfirmedArrive/>
      <SliderPlace/>
    </div>
    
  );
};

const ContentStyle = {
  display: 'flex',
  flexDirection: 'column', 
}

export default Content;
