import React, { useState } from "react";
import { Map, Placemark, YMaps } from "@pbe/react-yandex-maps";
import "./SliderPlace.css";
import { useSelector } from "react-redux";

const YandexMap = () => {
  return (
    <YMaps>
      <div className="slider-place-map">
        <Map
          defaultState={{
            center: [56.644099, 47.916389],
            zoom: 15,
          }}
          style={{ width: "100%", height: "100%" }}
        >
          <Placemark geometry={[56.644099, 47.916389]} />
        </Map>
      </div>
    </YMaps>
  );
};

const placePhotos = [
  {
    title: "Place 1",
    src: "/photos/place/place1.png",
  },
  {
    title: "Place 2",
    src: "/photos/place/place2.png",
  },
  {
    title: "Place 3",
    src: "/photos/place/place3.png",
  },
  {
    title: "Place 4",
    src: "/photos/place/place4.png",
  },
];

const SliderPlace = () => {
  const { backgroundColor, textColor, headerColor } = useSelector(
    (state) => state.app.sliderPlaceColorStyle
  );
  const [currentIndex, setCurrentIndex] = useState(0);

  const next = () => {
    setCurrentIndex((currentIndex + 1) % placePhotos.length);
  };

  const prev = () => {
    setCurrentIndex(
      (currentIndex - 1 + placePhotos.length) % placePhotos.length
    );
  };

  return (
    <div className="slider-place">
      <h2 style={{ color: headerColor}}> </h2>
      <div className="slider-place-content">
        <button
          className="slider-btn-prev"
          style={{ backgroundColor: backgroundColor, borderColor: textColor }}
          onClick={prev}
        >
          <img src="/icons/left-arrow.png" alt="" />
        </button>
        <img
          src={placePhotos[currentIndex].src}
          alt={placePhotos[currentIndex].title}
          className="slider-image"
        />
        <button
          className="slider-btn-next"
          style={{ backgroundColor: backgroundColor, borderColor: textColor }}
          onClick={next}
        >
          <img src="/icons/right-arrow.png" alt="" />
        </button>
      </div>
      <div className="slider-place-adress">
        <div
          className="slider-place-adress-info"
          style={{ backgroundColor: backgroundColor }}
        >
          <h2 style={{ color: textColor }}>      </h2>
          <p style={{ color: textColor }}>
            .  , . 61, . -,  
            , - " "
          </p>
          <div
            className="slider-place-open-btn"
            style={{ color: textColor, borderColor: textColor }}
            onClick={() =>
              window.open(
                "https://yandex.ru/maps/41/yoshkar-ola/?ll=47.911375%2C56.640647&mode=whatshere&whatshere%5Bpoint%5D=47.916397%2C56.644057&whatshere%5Bzoom%5D=null&z=15.8",
                "_blank"
              )
            }
          >
             
          </div>
        </div>
        <YandexMap />
      </div>
    </div>
  );
};

export default SliderPlace;
