import React from "react";
import "./DressCode.css";
import { useDispatch } from "react-redux";
import { changeAppColor } from "../slices/appSlice";

const colors = ["#595E48", "#919682", "#C7A491", "#EECFCC", "#ECE1DD"];

const ColorCircle = ({ color, borderColor, status, callback }) => {
  return (
    <div
      className="color-circle"
      style={{ backgroundColor: color, borderColor: borderColor }}
      onClick={() => callback(color)}
    > {status &&
        <div className="color-circle-mini" style={{backgroundColor: color, filter: "brightness(80%)"}}/>
    }
    </div>
  );
};

const ColorChooser = ({coloerChoosen, borderColor, changeColor}) => {
    return (
        <div className="dress-code-colors">
        {colors.map((color) => (
          <ColorCircle
            key={color}
            color={color}
            borderColor={borderColor}
            status={coloerChoosen === color}
            callback={changeColor}
          />
        ))}
      </div>
    );
}

const DressCode = () => {
  const [bgColor, setBgColor] = React.useState("#595E48");
  const [borderColor, setBorderColor] = React.useState("#919682");
  const [textColor, setTextColor] = React.useState("#ECE1DD");
  const dispatch = useDispatch();

  const changeColor = (color) => {
    switch (color) {
      case "#919682": {
        setBgColor("#919682");
        setBorderColor("#595E48");
        setTextColor("#ECE1DD");
        dispatch(changeAppColor("#595E48"));
        break;
      }
      case "#C7A491": {
        setBgColor("#C7A491");
        setBorderColor("#421716");
        setTextColor("#421716");
        dispatch(changeAppColor("#C9BFBC"));
        break;
      }
      case "#EECFCC": {
        setBgColor("#EECFCC");
        setBorderColor("#AF7E84");
        setTextColor("#AF7E84");
        dispatch(changeAppColor("#A98B7B"));
        break;
      }
      case "#ECE1DD": {
        setBgColor("#ECE1DD");
        setBorderColor("#AF7E84");
        setTextColor("#AF7E84");
        dispatch(changeAppColor("#AF7E84"));
        break;
      }
      default: {
        setBgColor("#595E48");
        setBorderColor("#919682");
        setTextColor("#ECE1DD");
        dispatch(changeAppColor("#919682"));
      }
    }
  };

  return (
    <div
      className="dress-code"
      style={{
        color: textColor,
        backgroundColor: bgColor,
        borderInline: bgColor === "#919682" ? `1px solid ${borderColor}` : "",
      }}
    >
      <h2 style={{ color: textColor }}>-</h2>
      <div
        className="dress-code-content"
        style={{ 
            color: textColor, 
            borderColor: borderColor,
        }}
      >
             
        <br />
            !
        <br />
        <br />
           
        <br />
         ,    .
        <br />
        <h3 style={{ color: textColor }}>~</h3>
            
        <br />
            
        <br />
            .
        <ColorChooser
          coloerChoosen={bgColor}
          borderColor={borderColor}
          changeColor={changeColor}
        />
      </div>
    </div>
  );
};

export default DressCode;
