import React, { useState, useEffect } from "react";
import "./AudioPlayer.css";
import { useSelector } from "react-redux";

const IconBtn = ({ pathToIcon, callback, rotate }) => {
  return (
    <div
      className="icon-btn"
      style={{
        width: "23%",
        height: "23%",
        borderRadius: "50%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        transform: rotate ? "rotate(180deg)" : "rotate(0deg)",
      }}
      onClick={callback}
    >
      <img src={pathToIcon} alt="" width="100%" height="auto" />
    </div>
  );
};

const AudioPlayer = () => {
  const { backgroundColor } = useSelector((state) => state.app.audioPlayer);
  const tracks = [
    {
      title: "Track 1",
      src: "/music/head.mp3",
    },
    {
      title: "Track 2",
      src: "/music/rings.mp3",
    },
    {
      title: "Track 3",
      src: "/music/yousad.mp3",
    },
  ];

  const [currentTrackIndex, setCurrentTrackIndex] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [isMuted, setIsMuted] = useState(false);
  const [volume, setVolume] = useState(0.2);
  const [currentAudio, setCurrentAudio] = useState(null);

  const playPause = () => {
    let audio = currentAudio;

    if (!audio) {
      audio = new Audio(tracks[currentTrackIndex].src);
      audio.volume = volume;
      audio.muted = isMuted;
      audio.addEventListener("ended", nextTrack);
      setCurrentAudio(audio);
    }

    if (isPlaying) {
      audio.pause();
    } else {
      audio.play().catch((err) => {
        console.error(" :", err);
      });
    }

    setIsPlaying(!isPlaying);
  };

  const loadAndPlay = (newIndex) => {
    if (currentAudio) {
      currentAudio.pause();
      currentAudio.removeEventListener("ended", nextTrack);
    }

    const audio = new Audio(tracks[newIndex].src);
    audio.volume = volume;
    audio.muted = isMuted;

    audio.addEventListener("ended", nextTrack);

    audio.play().catch((err) => console.error(" :", err));

    setCurrentAudio(audio);
    setCurrentTrackIndex(newIndex);
    setIsPlaying(true);
  };

  const prevTrack = () => {
    const newIndex = (currentTrackIndex - 1 + tracks.length) % tracks.length;
    loadAndPlay(newIndex);
  };

  const nextTrack = () => {
    const newIndex = (currentTrackIndex + 1) % tracks.length;
    loadAndPlay(newIndex);
  };

  useEffect(() => {
    return () => {
      if (currentAudio) {
        currentAudio.removeEventListener("ended", nextTrack);
        currentAudio.pause();
      }
    };
  }, [currentAudio]);

  const mute = () => {
    const newMutedState = !isMuted;
    setIsMuted(newMutedState);
    if (currentAudio) {
      currentAudio.muted = newMutedState;
    }
    if (!newMutedState) {
      setVolume(0.5);
    }
  };

  const VolumeSlider = () => {
    const handleVolumeChange = (e) => {
      const newVolume = parseFloat(e.target.value);
      setVolume(newVolume);
      if (currentAudio) {
        currentAudio.volume = newVolume;
      }
    };

    return (
      <div className="volume-slider-container">
        <input
          type="range"
          min="0"
          max="1"
          step="0.01"
          value={volume}
          onChange={handleVolumeChange}
          className="volume-slider"
        />
      </div>
    );
  };

  return (
    <div className="audio-player" style={{backgroundColor: `${backgroundColor}85`}}>
      <div className="audio-player-vinil">
        <div
          className={`audio-player-vinyl-rings ${isPlaying ? "spinning" : ""}`}
        >
          {["90%", "80%", "70%", "60%", "50%", "40%"].map((value) => {
            return (
              <div
                style={{
                  position: "absolute",
                  width: value,
                  height: value,
                  borderRadius: "50%",
                  borderTop: "1px solid #FFF",
                }}
              />
            );
          })}
        </div>
        <div className="audio-player-vinil-pin" />
        <VolumeSlider />
        <div className="audio-player-vinil-content">
          <IconBtn pathToIcon="/icons/prev-icon.png" callback={prevTrack} />
          <IconBtn
            pathToIcon={
              isPlaying ? "/icons/pause-icon.png" : "/icons/play-icon.png"
            }
            callback={playPause}
          />
          <IconBtn
            pathToIcon="/icons/prev-icon.png"
            callback={nextTrack}
            rotate={true}
          />
        </div>
        <div className="audio-player-center" />
      </div>
    </div>
  );
};

export default AudioPlayer;
