import React, { useEffect, useState, useCallback, useMemo } from "react";
import { motion } from "framer-motion";

import "./Countdown.css";

const Countdown = () => {
  const targetDate = useMemo(() => new Date("2025-08-29T00:00:00"), []);

  const calculateTimeLeft = useCallback(() => {
    const now = new Date();
    const difference = targetDate - now;

    if (difference <= 0) {
      return { days: 0, hours: 0, minutes: 0, seconds: 0 };
    }

    return {
      days: Math.floor(difference / (1000 * 60 * 60 * 24)),
      hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
      minutes: Math.floor((difference / 1000 / 60) % 60),
      seconds: Math.floor((difference / 1000) % 60),
    };
  }, [targetDate]);

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft);

  useEffect(() => {
    const timer = setInterval(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);

    return () => clearInterval(timer);
  }, [calculateTimeLeft]);

  const { days, hours, minutes, seconds } = timeLeft;

  if (days === 0 && hours === 0 && minutes === 0 && seconds === 0) {
    return <motion.h2 style={{marginTop: "4vw"}}> !</motion.h2>;
  }

  return (
    <div className="countdown">
      <AnimatedValue value={days} label="." />
      <AnimatedValue value={hours} label="." />
      <AnimatedValue value={minutes} label="." />
      <AnimatedValue value={seconds} label="." />
    </div>
  );
};

const AnimatedValue = ({ value, label }) => {
  return (
    <div className="countdown-item">
      <motion.span
        key={value}
        initial={{ y: -10, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{
          type: "spring",
          stiffness: 300,
          damping: 18,
        }}
        className="countdown-number"
      >
        {value.toString().length === 1 ? '0' + value.toString() : value.toString()}
      </motion.span>

      <span className="countdown-label">{label}</span>
    </div>
  );
};

export default Countdown;